<template>
    <Header />
    <Sidebar />
    <div class="LogPage">
        <h2 id="LogTitle">저장된 활동내역(로그)</h2>
        <div class="LogSerchTable">
            <br>
            <span id="LogTitleFont">기간</span>
            <input id='start_date' type="date" name="start_date" min="2000-01-01" max="2100-12-31" v-model="start_date">
            <input id="end_date" type="date" name="end_date" min="2000-01-01" max="2100-12-31" v-model="end_date">
            <input type="submit" value="검색" id="LogSubmit" @click="dateTest">
        </div>
        <pre><hr></pre>
        <div class="LogPageResult" v-for="item, index in test" :key="index">
            <h4 id="tableTitle">{{ dateTilte[index] }}</h4>
            <table id="LogResultTable">
                <tr id="LogResult_tr">
                    <th>이름</th>
                    <th>파일명</th>
                    <th>처리내용</th>
                </tr>
                <tr v-for="items,index in test" :key="index">
                    <td>{{item}}</td>
                    <td></td>
                    <td></td>
                </tr>
                <tr v-if="dateSerch_rows == 1 ">
                <td></td>
                </tr>
            </table>
            <hr>
        </div>
    </div>
</template>
<script>
import Header from '@/components/Header.vue';
import Sidebar from '@/components/Sidebar.vue';
export default {
    name: 'LogPage',
    components: { Header, Sidebar, },
    data: () => {
        return {
            test: [0,1, 2, 3, 4, 5, 6,7],
            dateTilte: ['오늘','어제','2일전','3일전','4일전','5일전','6일전','7일전'],
            reg_time: '',
            dateSerch_rows: 0,
        }
    },
    methods: {
        dateTest() {
            let startDate = new Date(document.getElementById('start_date').value)
            let fromYear = String(startDate.getFullYear())
            let fromMonth = String(startDate.getMonth() + 1)
            fromMonth = fromMonth.length == 1 ? '0' + fromMonth : fromMonth
            let fromDay = `${startDate.getDate() < 10 ? '0' + startDate.getDate() : startDate.getDate()}`;

            if (isNaN(fromYear) || isNaN(fromMonth) || isNaN(fromDay)) {
                fromYear = 0;
                fromMonth = 0;
                fromDay = 0;
            }
            startDate = fromYear + '년' + fromMonth + '월' + fromDay + '일';
            let endDate = new Date(document.getElementById('end_date').value)
            let toYear = String(endDate.getFullYear());
            let toMonth = String(endDate.getMonth() + 1);
            toMonth = toMonth.length == 1 ? '0' + toMonth : toMonth
            let toDay = `${endDate.getDate() < 10 ? '0' + endDate.getDate() : endDate.getDate()}`;

            if (isNaN(toYear) || isNaN(toMonth) || isNaN(toDay)) {
                toYear = 0;
                toMonth = 0;
                toDay = 0;
            }
            endDate = toYear + '년' + toMonth + '월' + toDay + '일';

            let dateForm = new Date();
            let todayYears = dateForm.getFullYear()
            let todayMonth = dateForm.getMonth() + 1
            todayMonth = todayMonth.length == 1 ? '0' + todayMonth : todayMonth
            let todays = `${dateForm.getDate() < 10 ? '0' + dateForm.getDate() : dateForm.getDate()}`;

            dateForm = todayYears + '년' + todayMonth + '월' + todays + '일';

            let overMonth = dateForm.substring(5, 7) - startDate.substring(5, 7)
            let overYears = dateForm.substring(0, 4) - startDate.substring(0, 4)
            console.log(overMonth)

            if (startDate.length != dateForm.length && endDate.length != dateForm.length || startDate.length == dateForm.length && endDate.length != dateForm.length || endDate.length == dateForm.length && startDate.length != dateForm.length) {
                swal('조회하시려는 기간을 다시한번 확인해주세요.')
                return this.dateSerch_rows = 0
            }
            else if (overYears > 0 || overMonth > 1 || !dateForm.substring(0, 4)) {
                swal('찾을 수 없는 기간입니다.')
                return this.dateSerch_rows = 0
            }
            else if (startDate > dateForm || endDate > dateForm) {
                swal('현재 날짜보다 시작일이나 종료일이 클 수 없습니다.')
                return this.dateSerch_rows = 0
            }
            else if (startDate > endDate) {
                swal('시작일을 종료일보다 크게 설정할 수 없습니다.')
                return this.dateSerch_rows = 0
            }
            else {
                swal(`${startDate}부터 ${endDate}까지의 로그를\n 조회하시겠습니까?`, {
                    buttons: ['No', 'Yes'],
                });
                return this.dateSerch_rows = 1
            }
        },
    }
}
</script>
<style lang="scss" scoped>
.LogPage {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 10%;
}
#tableTitle {
    border: 2px solid rgba(32,32,32,0.5);
    width: 4rem;
    height: 1.5rem;
    border-radius: 10px;
    text-align: center;
}

#start_date,
#end_date {
    width: 150px;
    text-align: center;
    color: black;
    border-radius: 4px;
}

.LogSerchTable {
    width: 100%;
    height: 50%;
    margin-bottom: 1px
}

#LogSubmit {
    margin-left: 10px;
    width: 5rem;
    height: 30px;
    border: 1px solid black;
    border-radius: 8px;
    background-color: black;
    color: white;
    font-size: large;
    cursor: pointer;

    &#LogSubmit:hover {
        box-shadow: inset 0 0 10px #fff;
    }
}

#LogTitleFont {
    font-size: larger;
    font-weight: bold;
    margin-right: 10px;
}

#LogResultTable,
th,
td {
    text-align: center;
    border-collapse: collapse;
    border: 1px solid black;
    height: 50%;
}

th {
    background-color: rgba(32, 32, 32, 0.2);
}

td {
    background-color: white;
}

#LogResultTable {
    width: 50rem;
    height: 10vh;
    box-shadow:  0 0 5px black;
    margin-bottom: 32px;
}
</style>